<template lang="pug">
  div.container
    .textarea__block
      textarea.textarea__top(v-model="map" placeholder="Места старта и финиша")
      textarea.textarea__top(v-model="time" placeholder="Время проведения")
      textarea.textarea__top(v-model="desc" placeholder="Доп. информация")

    .input__box
      .input__block
        input.input.link(v-model="link" placeholder="Ссылка на конвой")
        input.input.cargo(v-model="cargo" placeholder="Груз")
        input.input.discord(v-model="discord" placeholder="Discord (Если чужой)")
        button(@click="isAnnounce()") Создать
      .input__block
        .radio
          label.radio
            input(type="radio" v-model="route" value="Оптимальный")
            | Оптимальный
          label.radio
            input(type="radio" v-model="route" value="Кратчайший")
            | Кратчайший
          label.radio
            input(type="radio" v-model="route" value="По региональным дорогам")
            | По региональным дорогам

        .checkbox
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Beyond the Baltic Sea")
            | Beyond the Baltic Sea
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Going East!")
            | Going East!
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Italia")
            | Italia
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Scandinavia")
            | Scandinavia
          label.checkbox
            input(type="checkbox" v-model="dlc" value="Vive la France !")
            | Vive la France !
 
    div.vk
      pre(v-model="info") {{ infoTime.server }}
        br
        | {{ info.contact }}
        br
        | {{ info.route }}
        br
        br
        | {{ info.other }}
        br
      pre(v-model="info" v-for="item in info.dlc")
        | {{ item }}
      pre(v-model="info")
        | Старт: {{ infoMap.startCity }} ( {{ infoMap.startCompany }} )
        br
        | Финиш: {{ infoMap.finishCity }} ( {{ infoMap.finishCompany }} )
        br
        | {{ infoTime.meet }}
        br
        | {{ infoTime.exit }}
        br
        | {{ info.cargo }}
    
    hr
    hr
      
    div.discord
      pre(v-model="info") @everyone 
        br
        | Старт: {{ infoMap.startCity }}
        br
        | {{ infoTime.meet }}
        br
        | Подробнее:
        br
        | https://vk.com/vtcda
        br
        | {{ info.vtcpanel }}

    .bottom__block
      textarea.textarea__bottom(placeholder="vk")
      textarea.textarea__bottom(placeholder="discord")
    
</template>


<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      map: "",
      time: "",
      desc: "",
      link: "2",
      cargo: "Груз: ",
      route: "Оптимальный",
      discord: "",
      dlc: [],
      infoMap: {
        startCity: "",
        startCompany: "",
        finishCity: "",
        finishCompany: ""
      },
      infoTime: {
        server: "",
        meet: "",
        exit: ""
      },
      info: {
        contact: "",
        other: "",
        vtcpanel: "",
        cargo: "",
        route: "",
        dlc: []
      }
    };
  },
  methods: {
    isAnnounce() {
      if (this.link !== "") {
        let arrMap = this.map.split("\n");
        arrMap.splice(0, 1);
        arrMap.splice(2, 4);

        let n = 0;
        for(let item in this.infoMap) {
          this.infoMap[item] = arrMap[n]
          n++
        }


        let arrTime = this.time.split("\n");
        arrTime.splice(3);

        let m = 0;
        for (let item in this.infoTime) {
          this.infoTime[item] = arrTime[m];
          m++;
        }

        this.info.other = this.desc;
        this.info.cargo = this.cargo;
        this.info.vtcpanel = this.link;
        this.info.route = `Предпочитаемый маршрут: ${this.route}`;
        if (this.discord === "") {
          this.info.contact = "Связь: https://discord.gg/JSGZVph";
        } else {
          this.info.contact = this.discord;
        }
      }
      if (this.dlc.length !== 0) {
        this.info.dlc = [];
        this.dlc.forEach(el => {
          this.info.dlc.push(el);
        });
      }
    }
  }
};
</script>

